/*
 * 图片上传
 */
.list-img ul{
  float: left;
  padding-left: 0;
  margin-top: -10px;
  margin-bottom: 0;
}
.list-img li{
  float: left;
  position: relative;
  width: 90px;
  height: 90px;
  margin-right: 10px;
  margin-top: 10px;
  border-radius: 3px;
  background-color: #eee;
  overflow: hidden;
}
.list-img .pic{
  display: table;
  width: 100%;
  height: 100%;
}
.list-img .pic .inner{
  width: 100%;
  height: 100%;
  display: table-cell;
  vertical-align: middle;
}
.list-img .pic img{
  width: 100%;
}
.list-img .opt{
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 1;
  padding: 3px 20px 0;
  color: #fff;
  text-align: center;
  line-height: 20px;
  background-color:rgba(0,0,0,.5);
}
.list-img .opt a{
  display: block;
  margin-top:6px;
}
.list-img li:hover .opt{
  opacity: 1;
}
.list-img .upload{
  float: left;
  display: block;
  border:1px solid #ddd;
  border-radius: 3px;
  width: 90px;
  height: 90px;
  line-height: 90px;
  text-align: center;
}
.list-img .upload .ion{
  margin-top: 19px;
  font-size: 50px;
  color: #ccc;
}
.list-img .upload:hover{
  background-color: #fafafa;
}

.list-file li{
  padding: 5px 10px;
  margin-bottom: 10px;
  border-radius: 3px;
  background-color: #fafafa;
}
.list-file li .ion{
  display: inline-block;
  font-size: 16px;
  line-height: 18px;
  width: 20px;
}
.list-file li a{
  margin-left: 5px;
}
.list-file li .opt{
  float: right;
}
.list-file li:hover .opt{
  opacity: 1;
}

.list-img-multiple .upload{
  float: none;
  margin-top: 10px;
}
.list-img-multiple ul{
  float: none;
}
.list-img-multiple li{
  float: none;
  width: auto;
  height: auto;
  overflow: visible;
  background: none;
  margin-right: 0;
}
.list-img-multiple li:last-child{
  padding-bottom: 10px;
}
.list-img-multiple .left{
  width: 90px;
  float: left;
}
.list-img-multiple .pic-wraper{
  width: 90px;
  height: 90px;
  margin-bottom: 5px;
  overflow: hidden;
  border-radius: 3px;
  background-color: #eee;
}
.list-img-multiple .info-form{
  margin-left: 100px;
}
.list-img-multiple .info-form textarea{
  height: 60px;
  resize: none;
}
.list-img-multiple .info-form .form-control{
  margin-top: 10px;
}
.list-img-multiple .opt{
  position: static;
  opacity: 1;
  background: none;
  text-align: left;
  padding:5px 0 0;
  line-height: normal;
  height: 30px;
}
.list-img-multiple .opt a{
  margin-top:0;
  margin-right: 10px;
  display: inline-block;
}
.list-img-multiple .upload-sort{
  position: absolute;
  right: -10px;
  top: 0;
  bottom: 0;
  z-index: 1;
  width: 26px;
  text-align: center;
  cursor: move;
  background-color: #eee;
  opacity: .1;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
.list-img-multiple .upload-sort:before{
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: block;
  width: 6px;
  background-color: #fff;
}
.list-img-multiple li:hover .upload-sort{
  opacity: 1;
}
.list-img-multiple .upload-sort .ion{
  position: absolute;
  top: 50%;
  left: 4px;
  margin-top: -13px;
  vertical-align: middle;
  font-size: 22px;
  line-height: 26px;
}
.list-img-multiple li:last-child .upload-sort{bottom: 10px;}
.list-img-multiple .drag-start{
  background-color: #fafafa;
}

.list-img-multiple .opt .upload_move_up,
.list-img-multiple .opt .upload_move_down{
  margin-right: 0;
  margin-left: 5px;
  opacity: .2;
  margin-top: 3px;
  -webkit-transition: opacity .15s linear;
  -o-transition: opacity .15s linear;
  transition: opacity .15s linear;
}
.list-img-multiple li:hover .opt .upload_move_up,
.list-img-multiple li:hover .opt .upload_move_down{
  -webkit-transition: opacity .15s linear;
  -o-transition: opacity .15s linear;
  transition: opacity .15s linear;
  opacity: 1;
}

.list-img-multiple li:first-child .upload_move_up{
  opacity: .1;
}
.list-img-multiple li:last-child .upload_move_down{
  opacity: .1;
}
.list-img-multiple li:first-child:hover .opt .upload_move_up,
.list-img-multiple li:last-child:hover .opt .upload_move_down{
  opacity: .1;
}


/*
 * 缩略图背景图片
 */

.thumbnail-bg{
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.thumbnail-bg-narrow{
  background-size: contain;
}

/*
 *  带手机的布局
 */
.dk-game-design{
  position: relative;
  min-height: 750px;
}
.dk-game-design .dk-game-design-left{
  position: absolute;
  left: 0;
  top: 10px;
  width: 345px;
}
.dk-game-design .dk-game-design-phone{
  position: relative;
  width: 345px;
  height: 732px;
}
.dk-game-design .dk-game-design-phone>img{
  position: absolute;
  left:0;
  right: 0;
  bottom: 0;
  top:0;
  z-index: 1;
}
.dk-game-design .dk-game-design-phone-content{
  position: absolute;
  z-index: 2;
  left: 12px;
  right: 13px;
  top: 77px;
  bottom: 86px;
}

.dk-game-design .dk-game-design-form{
  margin-left: 385px;
}

.dk-game-design-upload{
  min-height: 840px;
}
.dk-game-design-upload .dk-game-design-left .upload_change,
.dk-game-design-upload .dk-game-design-left .upload_btn{
  position: absolute;
  z-index: 3;
  bottom: -74px;
  left: 50%;
  margin-left: -41px;
}
.dk-game-design-upload .dk-game-design-left .upload_change{
  bottom: -160px;
}

.dk-game-design-element{
  margin: 10px 0;
  padding:  15px 5px 0;
  border-radius: 4px;
  background-color: #fcfcfc;
}
.dk-game-design-element .upload.upload_btn{
  margin-bottom: 10px;
}
.dk-store-edit-element {
  padding-left: 15px;
  padding-right: 15px;
}

.dk-nav-tabs-tools-wrapper .nav{
  min-height: 42px;
}

.dk-nav-tabs-tools-wrapper .dk-nav-tabs-tools{
  float: right;
  margin-top: -38px;
}



